.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 280px);
  gap: 20px;
  justify-content: center;
}
.list__item {
  width: 280px;
  position: relative;
  transition: transform 0.3s ease;
}
.list__item:hover {
  transform: scale(1.03);
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
}

.list__item:hover .overlay {
  opacity: 1;
  visibility: visible;
}

.list__item_title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
}

.img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@media screen and (max-width: 767px) {
  .list__item {
    width: 100%;
    height: 450px;
  }
}
